import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import dataSource from './views/nav1/dataSource.vue'
import metaData from './views/nav1/metaData.vue'
import batch from './views/task/batch.vue' // 
import schedule from './views/task/schedule.vue' // 
import log from './views/log/index.vue'
import result from './views/log/result.vue'
import product from './views/product/index.vue' // 
import echarts from './views/charts/echarts.vue'
import nav from './views/charts/index.vue'
import page7 from './views/metaDatasource/datasource.vue'

let routes = [
  {
    path: '/login',
    component: Login,
    name: '',
    hidden: true
  },
  {
    path: '/404',
    component: NotFound,
    name: '',
    hidden: true
  },
  // { path: '/main', component: Main },
  {
    path: '/',
    component: Home,
    name: '',
    iconCls: 'fa fa-adjust', // class
    children: [
      { path: '/main', component: Main, name: '', hidden: true },
      { path: '/dataSource', component: dataSource, name: '' },
      // { path: '/ceshi', component: ceshi, name: '' },

    ]
  },
  {
    path: '/',
    component: Home,
    name: '',
    leaf: true, // 
    iconCls: 'fa fa-product-hunt',
    children: [
      { path: '/product', component: product, name: ''}
    ]
  },
  {
    path: '/',
    component: Home,
    name: '',
    iconCls: 'fa fa-th-list',
    children: [
      { path: '/batch', component: batch, name: '' },
      { path: '/schedule', component: schedule, name: '' }
    ]
  },
  {
    path: '/',
    component: Home,
    name: '',
    leaf: true, // 
    iconCls: 'fa fa-file',
    children: [
      { path: '/log', component: log, name: '' },
    // { path: '/result', component:result, name: '' },
    ]
  },
  // {
  //     path: '/',
  //     component: Home,
  //     name: '',
  //     iconCls: 'fa fa-address-card',
  //     leaf: true,//
  //     children: [
  //         { path: '/log', component: log, name: '' },
  //     ]
  // },
  // {
  //     path: '/',
  //     component: Home,
  //     name: '',
  //     iconCls: 'fa fa-address-card',
  //     leaf: true,//
  //     children: [
  //         { path: '/log', component: log, name: '' },
  //     ]
  // },
  // {
  //     path: '/',
  //     component: Home,
  //     name: 'Charts',
  //     iconCls: 'fa fa-bar-chart',
  //     children: [
  //         { path: '/nav', component: nav, name: '' }
  //     ]
  // },
  {
    path: '*',
    hidden: true,
    redirect: { path: '/404' }
  }
]
export default routes



